/*************** Common *************/
main {
  padding-top: 1em;
  padding-bottom: 1em;
}

.h1, h1 {
  font-size: 1.5rem;
}

.h2, h2 {
  font-size: 1.2rem;
}

.h3, h3 {
  font-size: 1rem;
}

.h4, h4 {
  font-size: 0.8rem;
}

/*************** Sticky footer **************/
html {
  position: relative;
  min-height: 100%;
}

body {
  background-image: url(/static/images/bg.jpg);
  background-attachment: fixed;
  background-size: cover;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #fff;
  text-align: center;
}
.footer > .container {
  padding-right: 15px;
  padding-left: 15px;
}

/*************** Main page *************/
.main-image {
  background-image: url(/static/images/bg.png);
  background-size: cover;
}

.main-header {
  text-shadow: 2px 2px #020122;
}

.quick-reference-wrapper {
  display: inline-block;
  margin-top: 25px;
  position: relative;
}
.quick-reference-wrapper img {
  display: block;
  max-width: 100%;
}
.quick-reference-wrapper .quick-reference-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
  color: white;
}
.quick-reference-wrapper .quick-reference-overlay .quick-reference-text {
  bottom: 0;
  position: absolute;
  text-align: center;
  width: 100%;
  font-size: 3em;
}

/****** Product list ******/
.list-display-type label {
  cursor: pointer;
}
.list-display-type label > input {
  display: none;
  position: absolute;
}
.list-display-type .btn {
  padding: 0.15rem 0.5rem;
}

.product-img-thumbnail {
  background-color: #fff;
}
.product-img-thumbnail:hover {
  background-color: #352D39;
}

/****** Image carousel *******/
.carousel-thumbnails-wrapper ol.carousel-thumbnails {
  list-style: none;
  display: flex;
  overflow: hidden;
  padding: 0;
  margin-bottom: 0;
}
.carousel-thumbnails-wrapper ol.carousel-thumbnails .img-carousel-thumbnail {
  width: 100px;
  height: 100px;
  margin: 0 2px;
}
.carousel-thumbnails-wrapper ol.carousel-thumbnails .img-carousel-thumbnail.active img {
  box-shadow: inset 0px 0px 40px 40px #020122;
}
.carousel-thumbnails-wrapper ol.carousel-thumbnails .img-carousel-thumbnail img {
  background-color: #fff;
}
.carousel-thumbnails-wrapper ol.carousel-thumbnails .img-carousel-thumbnail img:hover {
  box-shadow: inset 0px 0px 40px 40px #352D39;
}
.carousel-thumbnails-wrapper ol.carousel-thumbnails .img-carousel-thumbnail img.img-thumbnail {
  max-width: 100px;
  height: 100px;
  -o-object-fit: cover;
  object-fit: cover;
}
.carousel-thumbnails-wrapper .carousel-control-prev,
.carousel-thumbnails-wrapper .carousel-control-next {
  margin-bottom: 100px;
}

/****** Cart *******/
.cart-image {
  max-height: 7rem;
  margin-right: 0.5rem;
}

